.news{
    img{
        @media (min-width: $mdMedia) { display:none; }
    }
    .img{
        display:none;
        background-size: cover;
        height:100%;
        background-position:center;
        @media (min-width: $mdMedia) { display:block; }
        &.shit{
            @media (min-width: $lgMedia) { 
                height: 115%;
                border-top: 15px solid #fff;
                border-left: 15px solid #fff;
                margin-top: -15%;
            }
            @media (min-width: $xlMedia) { 
                height: 120%;
                
            }
        }
    }
    .women-news ,.men-news{
        h3{
            font-weight: 300;
            text-transform: uppercase;
            letter-spacing: 1px;
        }
        p{
            
            line-height: 1.8;
            @extend %p;
            color: gray;
        }
        button{
            color:$blackColor;
            background-color:#fff;
            transition:$transition;
            box-shadow: 0px 24px 36px -11px rgba(0, 0, 0, 0.09);
            border-radius:30px;
            &:hover{
                background-color:$mainColor;
                color:#fff;
            }
        }
        
    }
    .women-news{
        @media (min-width:$lgMedia){
            width:200%;
            margin-left:-90%;
        }
        @media (min-width:$xlMedia){
            width:150%;
            margin-left:-50%;
        }
    }
}