footer{
    background-color:$bgGrayColor;
    padding:$sectionPadding;
    @extend %relative;
    .scrollTop{
        border-radius:50%;
        @extend %flex-center;
        padding:15px;
        color:$blackColor;
        font-size:16px;
        @extend %absolute;
        top:0;
        left:50%;
        transform: translate(-50%,-50%);
        transition:$transition;
        background: #fff;
        border:none;
        &:focus{
            border:none;
        }
        &:hover{
            background: $mainColor;
            color:#fff;

        }
        svg{
            animation: top-down 2s infinite linear ;
        }
    }
    h6{
        @extend %smallHeading;
        margin-bottom:20px;
        text-transform: uppercase;
        letter-spacing: 3px;
    }
    .footer-one{
        
        p{
            @extend %p;
            color:$blackColor;
        }
        .icons{
            span{
                display:inline-flex;
                @extend %flex-align;
                @extend %flex-justify;
                width:55px;
                height:55px;
                background-color:#F3F4F5;
                transition:$transition;
                font-size:18px;
                border-radius: 50%;
                @media(min-width: $lgMedia){
                    margin-bottom:10px
                }
                @media(min-width: $xlMedia){
                    margin-bottom:0px
                }
                cursor: pointer;
                &:not(:last-of-type){
                    margin-right:10px;
                }
                &:hover{
                    background-color:$mainColor ;
                    color: #fff;
                }
            }
        }
    }
    .footer-two{
        a{
            display:block;
            font-size:$smallHeadingFontSize;
            color:$blackColor;
        }
    }
    .footer-three{
        span{
            margin-right:10px;
        }
        p,a{
            display:block;
            font-size:$smallHeadingFontSize;
            color:$blackColor;
            word-break:break-word;
        }
    }
    .copy{
        text-align: center;
        @extend %p;
        color:$blackColor;
        span{
            color:$mainColor;
        }
    }
}

@keyframes top-down{
    0%{
        transform: translateY(0)
    }
    25%{
        transform: translateY(-6px);
        
    }
    35%{
        transform: translateY(-8px);
        opacity: .5;
    }
    50%{
        transform: translateY(-10px);
        opacity: 0;
    }
    65%{
         transform: translateY(10px);
        opacity: 0;
    }
    75%{
        transform: translateY(8px);
        opacity: .5;
    }
    100%{
        transform: translateY(0px);
        opacity: 1;
    }
}




