
.visitors{

    background-size: cover;
    background-position: center;
    div{
       
        flex-wrap:wrap;
        justify-content:center;
        @extend %flex-center;
        p{
            width:100%;
            margin:0;
            &:first-of-type{
                font-weight: 400;
                font-size: 30px;
                color: #000000;
                margin-bottom:5px;
            }
            &:last-of-type{
                display: block;
                font-size: 12px;
                color: rgba(0, 0, 0, 0.7);
                text-transform: uppercase;
                letter-spacing: 1px;
            }
        }
    }
}