.carousel-indicators{
        li{
            background-color:$mainColor !important;
        }
    
}
.slide{
    @extend %relative;
    @extend %flex-center;
    height:100vh;
    @media (min-width: $lgMedia) {
        position: initial;
        justify-content: initial;
        flex-wrap: nowrap;
    }
    .img{
        width:100%;
        height:100%;
        @media (min-width: $mdMedia) { 
            background-position:0% 30% !important;
         }
        @media (min-width: $lgMedia) { 
            width:50%;
            order:2;
         }
        
    }
    .caption{
        @extend %absoluteCenter;

        text-align: center;
        width:80%;
        color:#fff;
        @media (min-width: $lgMedia) { 
            position: initial;
            top:initial;
            left:initial;
            transform:initial;
            text-align: left;
            width:50%;
            padding:0 20px 0 60px;
            color:$headingColor;
            height:100%;
            display:flex;
            align-items: center;
            flex-wrap: wrap;
         }
         @media (min-width: $xlMedia) { 
             padding:0 40px 0 90px;
          }
        span{
            h6{
            
                @extend %smallHeading;
                letter-spacing: 2px;

                // margin-bottom: 30px;
                @media (min-width: $mdMedia) { 
                    font-size:16px;
                 }
                 @media (min-width: $lgMedia) { 
                    font-size:12px;
                    font-weight:200;
                    letter-spacing: 3px;
                    &::after{
                        content:"";
                        
                        display: block;
                        width: 120px;
                        height: 1px;
                        background: #000;
                        // position: absolute;
                        // top: 31.5%;
                        // left: 3%;
                        margin:15px 0;
                        transform: translateX(-30%);
                        
                    }
                 }
                 @media (min-width: $xlMedia) { 
                     margin-right: 8px;
                     &::after{
                         width:100px;
                         
                         transform: translateX(-40%);
                     }
                  }
            }
            h2{
                
                font-weight:bold;
                @extend %bigHeading;
                text-transform: capitalize;

                @media (min-width: 768px) { 
                    font-size:50px;
                 }
                 @media (min-width: 992px) { 
                   font-size:$bigHeadingFontSize;
                 }
                 @media (min-width: 1200px) { 
                     font-size:45px;
                  }
            }
            p{
                color:rgba(#fff,.8);
                font-size:23px;
                letter-spacing: 1px;
                margin-bottom: 30px;
                @media (min-width: 768px) { 
                    font-size:25px;
                    line-height: 2;
                 }
                 @media (min-width: 992px) { 
                    font-size:14px;
                    color:rgba(#000,.8)
                 }
                 @media (min-width: 1200px) { 
                     font-size:16px;
                  }
            }
            h5{
                display:none;
                @media (min-width: 1200px) { 
                    display:block;
                    
                    font-size:12px;
                    font-weight:200;
                    letter-spacing: 3px;
                    
                    transform: translate(-58%, -1650%) rotate(-90deg);
                 }
            }
            button{
                
                
                border: 2px solid $mainColor;
                font-size: 14px;
                font-weight: 600;
                color: #fff;
                background-color: $mainColor;
                padding: 15px 40px;
                border-radius: 25px;
                transition:$transition;
                box-shadow: 0px 2px 15px 1px #ccc;
                &:hover{
                    background-color:transparent;
                    color:$mainColor;
                }
        }
            
        }
    }
}



