.cart-component{
    padding:$sectionPadding;
    .cartNav{
        color:#fff;
        background-color:#000;
    }
    .cart-item{
        border-bottom: 1px solid #ccc;
    }
    .close{
        width:30px;
        height:35px;
        font-size:14px;
        font-weight:bold;
        border:.5px solid #ccc;
        text-align: center;
        line-height: 35px;
        cursor: pointer;
        transition:$transition;
        &:hover{
            color:#fff;
            border-color: $mainColor;
            background-color:$mainColor;
        }
    }
    .product-info{
        h4{
            @extend %smallHeading;
            letter-spacing: 3px;
        }
        p{
            @extend %p;
            color:gray;
        }
    }
}