.modal{
    background-color:rgba($headingColor,.8);
    position: fixed;
    top:0;
    left:0;
    z-index: 10000;
    @extend %flex-align;
    @extend %flex-justify;
    width:100%;
    height:100vh;
    display: none;
    &.active{
        @extend %flex;
    }
    .close{
        @extend %absolute;
        top: 40px;
        right:40px;

        width:55px;
        height:55px;
        color:#fff;
        background-color:$mainColor;
        border:2px solid $mainColor;
        transition:$transition;
        border-radius:50%;
        @extend %flex-center;
        cursor: pointer;
        &:hover{
            background-color: transparent;
            color:$mainColor;
        }
    }
}