.services{
    background-color:$bgGrayColor;
    
    
    div.col-12.ig{
        padding:0;
        @media (min-width: $lgMedia) { 
            padding: 0 15px;
         }
    }
    .img{
        height:300px;

        background-size: cover !important;
        @extend %flex-center;
        @media (min-width: $mdMedia) { 
            height:100%;
            background-position: 50% 0% !important;
         }
        div{
            width:90px;
            height:90px;
            background-color:$mainColor;
            border-radius:50%;
            @extend %flex-center;
            animation: shadowing 1.5s infinite linear;
            cursor: pointer;
            span{
                display:block;
                clip-path: polygon(0 0, 0 100%, 100% 50%);
                background: #fff;
                width:20px;
                height:20px;
            }
        }
    }
    .info{
        padding:$sectionPadding;
        h2{
            color:$headingColor;
            
            font-weight: 600;
            @extend %bigHeading;
        }
        p{
            @extend %p;
            line-height: 1.8;

            color: gray;
            margin-bottom:20px;
        }
        div.e{
            height: 74px;
            width: 35px;
            border: 2px solid #ccc;
            text-align: center;
            line-height: 7px;
            margin: 0 auto 40px auto;
            font-size: 7px;
            font-weight: bold;
            text-transform: capitalize;
            
        }
        h5{
            @extend %smallHeading;
            text-transform: uppercase;

            margin-bottom: 20px;
            letter-spacing: 3px;
            text-align: center;
        }
        span{
           
            line-height: 1.8;
            @extend %smallHeading;
            color: gray;
            display:block;
            text-align: center;
            &:not(:last-of-type){
                margin-bottom:60px;
                @media (min-width: $lgMedia) { margin-bottom: 0; }
            }

        }
    }
}
@keyframes shadowing{
    0%{
        box-shadow:0px 0px 0px 0px rgba($mainColor,.3);
    }
    50%{
        box-shadow: 0px 0px 0px 15px rgba($mainColor,.3);
    }
    100%{
        box-shadow: 0px 0px 0px 30px rgba($mainColor,0);
    }
}

.iframe{
    width:100%;
    height:100%;
    @extend %flex-center;
    iframe{
        width:95%;
        height:60%;
    }
}

