*,
*::after,
*::before{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
html{
    scroll-behavior: smooth;
}
body{
    font-family: 'Open Sans', sans-serif !important; 
}
a{
    text-decoration:none !important;
}
ul{
    list-style: none;
}
.ani{
    opacity:0;
    transform:translateY(100px);
    
}