.header-img{
    @extend %flex-center;
    flex-wrap: wrap;
    background-size: cover;
    text-align: center;
    padding: 120px 0;
    background-position: 0% 40%;
    @media(min-width:$lgMedia){
        background-position: 0% 50%
    }
    
    h6{
        color:$blackColor;
        letter-spacing: 2px;
        @extend %smallHeading;
        width:100%;
        a{
            color:$blackColor;
        }
    }
    h2{
        color:$blackColor;
        font-weight: 600;
        font-size:30px;
        letter-spacing: 2px;
        width:100%;
    }
}