.product{
    @extend %flex;
    flex-wrap:wrap ;
    flex-direction: column;
    background-color:#fff;
    transition:$transition;
    overflow:hidden;
    &:hover{
        box-shadow:1px 2px 5px 5px #eeeded;
    }
    &:hover .img img{
        transform: scale(1.05);
    }
    &:hover .img .overlay{
        opacity: 1;
    }
    .img{
        display:block;
        width:100%;
        height: auto;
        @extend %relative;
        overflow: hidden;
        img{
            width:100%;
            transition: $transition;
            height:auto;
        }
        .overlay{
            @extend %absolute;
            opacity: 0;
            transition: $transition;
            top:0;
            left:0;
            width:100%;
            height:100%;
            background-color: rgba($mainColor,.4);
            a{
                display: block;
                background:#fff;
                @extend %absolute;
                font-size: 13px;
                color: $blackColor;
                text-transform: uppercase;
                font-weight: 300;
                bottom:20px;
                left:50%;
                transform: translatex(-50%);
                transition:$transition;
                &:hover{
                    background:$mainColor;
                    color:#fff;
                }
            }
        }
        .percent{
            @extend %absolute;
            top: 10px;
            left: -1px;
            padding: 2px 15px;
            color: #000000;
            font-weight: 300;
            background: #ffa45c;
        }
    }
    .white-part{
        .title{
            font-size: $smallHeadingFontSize;
            font-weight: 300;
            text-transform: uppercase;
            letter-spacing: 1px;
            color:$blackColor;
    
        }
        .info{
            @extend %flex;
            flex-wrap: nowrap;
            .price{
                color: #000000;
                @extend %p;
                span{
                    text-decoration: line-through;
                    color: #b3b3b3;

                }
            }
            
        }
    }
}