%p{
    font-size:$pFontSize;
    font-weight:400;
}
%bigHeading{
    font-size:40px;
    margin-bottom:35px;
}
%smallHeading{
    font-size:$smallHeadingFontSize;
    font-weight: 400;
}

%flex{
    display: flex;
}
%flex-align{
    align-items: center;
}
%flex-justify{
    justify-content: center;
}
%flex-center{
    @extend %flex;
    @extend %flex-align;
    @extend %flex-justify;
}
%absoluteCenter{
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
}
%absolute{
    position: absolute;
}
%relative{
    position: relative;
}