nav{
    background-color:$headingColor;
    padding:15px 10px ;
    @extend %flex;
    @extend %flex-align;
    transform: $transition;

    flex-wrap:wrap;
    @media (min-width: $smMedia) { 
        padding :15px 25px;
    }

    @media (min-width: $mdMedia) { 
        padding:15px 40px;
    } 
    @media (min-width: $lgMedia) { 
        background-color:#fff;
        flex-wrap:nowrap;
        padding:15px 60px;    
    }
    @media (min-width: $xlMedia) { 
        
        padding:15px 80px;    
    }
    &.fixed{
        position: fixed;
        top:-100px;
        left:0;
        width:100%;
        z-index: 10000000;
        padding:5px 10px;
        animation: nav .4s ease-out forwards;
        @media (min-width: $smMedia) { 
            padding :5px 25px;
        }

        @media (min-width: $mdMedia) { 
            padding:5px 40px;
        } 
        @media (min-width: $lgMedia) { 
            padding:10px 60px;    
        }
        @media (min-width: $xlMedia) { 

            padding:10px 80px;    
        }
    }
    .logo{
        color:#fff;

        font-weight: 800; 
        font-size: 20px;
        letter-spacing:2px;
        transform: translatey(7px);
        @media (min-width: 992px) { 
            color:$headingColor;
            transform: translatey(0px);
         }
    }
    .icon{
        margin-left:auto;
        color:#fff;
        font-size:20px;
        cursor: pointer;
        transform: translatey(6px);
        @media (min-width: 992px) { 
            display: none;
         }
    }
    .links{
    background-color: $blackColor;
    
    
    &.active{
        height: 230px;
        margin-top: 20px;
        @media (min-width: $lgMedia) { 
            height:auto;
            margin-top: 0;
        }
        
    }
    
    width:100%;
    height:0;
    margin-top: 0;
    transition: $transition;
    overflow: hidden;
    @media (min-width: $lgMedia) {
        background-color: white; 
        height: auto;
        width:auto;
        overflow:initial;
        transition:initial;
        display:block;
        margin:0 0 0 auto;
    }
    .link{
        
        @media (min-width: $lgMedia) { 
            display: inline-block;  
        }
        a{
            color:#fff;
            display: block;
            font-weight: 600;
            font-size:12px;
            letter-spacing: 1.2px;
            padding:14px 0;
            @media (min-width: $lgMedia) { 
                color:$headingColor; 
                padding:0 14px;   
            }
        }
        
        &.cart{
            background-color:$mainColor;
            @media (min-width: $lgMedia) { 
                background: transparent;
             }
        }
        
    }
    
}
}
@keyframes nav{
    0%{
        top:-100px;
    }
    100%{
        top:0;
    }
}
