.single-product{
    padding:$sectionPadding;
    @extend %relative;
    
    p{
        @extend %p;
        color:gray;
        word-break: break-word;
        line-height: 1.7;
    }
    select{
        font-size: 13px;
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        text-transform: uppercase;
        letter-spacing: 2px;
        
        color: #000000;
        background: transparent !important;
        height: 40px !important;
        text-align: center;
        border: 1px solid rgba(0, 0, 0, 0.1) !important;
        
        padding: 10px 20px;
        padding-right: 40px;
    }
    span.select-arrow{
           position: absolute;
            top: 503px;
            left: 34%;
            transform: translate(-60%,-51%);
            font-size: 25px;
    }
    .input{
        height:40px;
        text-align: center;
        button {
            border:1px solid rgba(0, 0, 0, 0.1) !important;
            background-color:#fff;
            font-size:14px;
            font-weight:bold;
            padding: 0 15px;
            margin:0 5px;
        }
        input {
            text-align: center;
            border: 1px solid rgba(0, 0, 0, 0.1) !important;;
        }
    }
    .pieces,.price{
        color:#000;
    }
    .price{
        span{
            color:gray;
            text-decoration: line-through;
            margin-right:5px;
        }
    }
    .addToCart{
        background-color: #000;
        color: #fff;
        font-weight: bold;
        font-size: 12px;
        border: none;
        padding: 20px 40px;
        border-radius: 30px;
        transition: $transition;
        &:hover{
            background-color: $mainColor;
        }
    }
    .img{
        background-size: cover;
        background-position: center;
    }
    
}
