.contact{
    padding:$sectionPadding;
    background-color:$bgGrayColor;
    .info{
        background-color:#fff;
        word-break: break-word;
        height:100%;
        color:lightgray;
        span,a{
            color:#000;
        }
        
    }
    form{
        background: #fff;
        @extend %flex;
        flex-wrap:wrap;
        div{
            padding: 15px;
            width:100%;
            @media(min-width:$lgMedia){
                &:first-of-type,&:nth-of-type(2){
                    width: 50%
                }
            }
            input{
                border:none;
                width:100%;
                border-bottom:1px solid #ccc;
                padding: 0 0 10px 5px;
                font-size:14px;
                transition: $transition;
                
                &:focus{
                    border-bottom: 1px solid $mainColor;
                    &:placeholder-shown{
                        color:$mainColor;
                    }
                }
            }
            textarea{
                font-size:14px;
                width:100%;
                height:125px;
                padding: 10px 0px 0px 5px;
                transition: $transition;
                &:focus{
                    border: 1px solid $mainColor;
                    &:placeholder-shown{
                        color:$mainColor;
                    }
                }
            }
            button{
                padding:17px 35px;
                background: $mainColor;
                font-size:12px;
                font-weight:bold;
                color:#fff;
                border: 2px solid $mainColor;
                transition: $transition;
                border-radius: 35px;
                &:hover{
                    color:$mainColor;
                    background: #fff;
                }
            }
        }
    }
}